@import 'reset';
@import 'typography';
@import 'mixins';
@import 'variables';

/* Styles */
html {
  font-size: 112.5%;
  color: $text;
  font-family: $sans;
}

body {
  background: $background;
  border: 0.4rem solid $text;
  border-bottom: none;
  line-height: $lh;
}

h1 {
  font-size: $fsh1;
  font-family: $slab;
}

h2 {
  font-size: $fsh2;
  font-family: $slab;

  &.tag {
    color: $light_purple;
    padding-bottom: $fsh3;
    text-align: center;
    line-height: 1;
  }
}

h3 {
  font-size: $fsh3;
}

h4 {
  font-size: $fsh4;
}

h5 {
  font-size: $fsh5;
}

h6 {
  font-size: $fsh6;
}

p,
ul,
ol {
  font-size: $fsp;
  padding: {
    top: 0;
    bottom: $fsp;
  }
  margin: {
    top: 0;
    bottom: 0;
  }
}

blockquote {
  font: {
    family: $slab;
    size: $fsp;
    style: italic;
  }
  margin: 0.5em 0;
  padding: 0 1em;
  border-left: 2px solid $pale_purple;
  color: $background_darkened;

  em {
    font-style: normal;
  }
}

blockquote p:last-child {
  padding-bottom: 0;
}

li blockquote {
  margin-bottom: 1.5rem;
}

ul {
  list-style: circle;
  ul {
    list-style: disc;
    ul {
      list-style: square;
      ul {
        list-style: circle;
        ul {
          list-style: disc;
        }
      }
    }
  }
}

ol {
  list-style: decimal;
  ol {
    list-style: upper-alpha;
    ol {
      list-style: upper-roman;
      ol {
        list-style: lower-alpha;
        ol {
          list-style-type: lower-roman;
        }
      }
    }
  }
}

ol ol {
}

a,
a:visited {
  color: $light_purple;
  transition: all 0.347s;
  text-decoration: none;
  border-bottom: 1px solid transparent;

  &:hover,
  &:active {
    color: $light_blue;
    border-bottom: 1px solid $background_darkened;
  }
}

cite {
  font-style: italic;
}

img {
  max-width: 100%;
  max-height: 100%;
}

figure {
  margin-bottom: $fsp;
  text-align: center;
  padding: 0.52em;
  font-weight: 300;
  font-size: $fs_small;
  line-height: $lh_small;
  font-style: italic;
}

.sponsors-list {
  text-align: center;
  font-size: $fsh6;
  margin: 0;
  padding: 0;
  list-style: none;

  &__sponsor {
    margin: 0;
    padding: 0 0 1em;

    &::after,
    &::before {
      content: '✨';
      display: inline-block;
      width: 2em;
    }
  }
}

/* Layout */
#wrapper {
  max-width: 44rem;
  padding: 0 1rem $fsh1;
  margin: auto;
}

br.optional {
  display: none;
}

/* Header */
header#banner {
  padding-top: $fsh3;
  padding-bottom: $fsh5;
  text-shadow: 1px 1px 1px #ddd;

  a {
    color: transparent;

    &:link,
    &:visited,
    &:hover,
    &:active {
      border-bottom: none;
    }
  }

  h1 {
    text-align: center;

    #logo,
    #winning,
    #slowly {
      display: inline-block;
    }

    #logo {
      img {
        height: $fs_large;
        width: $fs_large;
        vertical-align: middle;
        margin-top: -10px;
      }
    }

    #winning,
    #slowly {
      text-transform: uppercase;
      line-height: 1;
      margin: 0;
    }

    #winning {
      color: $dark_orange;
      font-weight: 300;
      font-family: $slab;
      letter-spacing: 3px;
      margin-right: -0.78rem;
    }

    #slowly {
      color: $light_orange;
      font-size: $felt_multiplier_em;
      font-family: $felt;
      margin-top: -10px;
    }
  }

  h2 {
    text-align: center;
    color: $light_purple;
    margin-top: -1rem;
    font-weight: 400;
    font-size: 1.2rem;
    padding-bottom: 2rem;
  }
}

/* Podcast items */
article {
  padding-bottom: $fsh1;
  line-height: 1.7;

  @mixin header-item() {
    margin-bottom: 1rem;
  }

  h1 {
    @include header-item();
    font-weight: 300;
  }

  h2 {
    @include header-item();
    font-weight: 700;
    text-align: center;
  }

  h3 {
    text-align: center;
    margin: {
      bottom: 1rem;
      left: auto;
      right: auto;
    }
    border-bottom: 1px solid $background_darkened;
  }

  header {
    text-align: center;
    .title {
      line-height: 0.9;
      h1 {
        #episode-num,
        #episode-title {
          display: block;
        }

        #episode-num {
          margin-bottom: 1rem;
        }

        #episode-title {
          font: {
            size: $fsh2;
            weight: 700;
          }
          text-transform: uppercase;
          letter-spacing: -1px;
        }
      }

      #episode-subtitle {
        font-size: $fsh5;
        padding-bottom: 0;
      }
    }

    .meta {
      font-family: $sans;
      font-weight: 700;
      color: $background_darkened;
    }

    ul.meta {
      line-height: $lh_small;
      display: inline-block;
      padding: {
        top: 1em;
        bottom: 0;
        left: 0;
        right: 0;
      }
      margin-bottom: 0;
      li {
        display: inline-block;
      }

      > li:after {
        content: '\25E6';
        font-size: $fs_small;
        padding: {
          left: 1px;
          right: 1px;
        }
      }

      > li:last-child:after {
        content: none;
      }
    }

    li:last-child:after {
      content: none;
    }
  }

  audio {
    display: block;
    text-align: center;
    width: 66.7%;
    margin: {
      top: $fsh2;
      bottom: $fsh3;
      left: auto;
      right: auto;
    }
  }

  #download {
    text-align: center;
  }

  #notes {
    @mixin all {
      max-width: 36rem;
      margin: {
        left: auto;
        right: auto;
      }
    }

    @mixin list() {
      @include all();
      padding-left: 1rem;
    }

    p {
      @include all();
    }

    ul {
      @include list();
      list-style: circle;
    }

    ol {
      @include list();
      list-style: decimal-leading-zero;
    }

    h4 {
      text-align: center;
    }
  }

  section {
    margin: {
      left: auto;
      right: auto;
    }
  }

  a.social-inline-link {
    display: inline-block;

    i {
      display: inline-block;
      padding-right: 2px;
    }
  }
}

.index {
  h4 {
    margin-top: 1em;
  }

  article {
    h4 {
      text-align: center;
    }
  }
}

.archive {
  .season {
    text-align: center;
    padding-bottom: $fsp;
  }

  article {
    min-height: 4em;
    padding-bottom: 2em;

    @mixin flex-container() {
      @include flexbox();
      @include justify-content(space-between);
      @include align-content(flex-start);
      @include flex-flow(row wrap);
    }

    @mixin flex-item() {
      @include flex(1 18rem);
    }

    header {
      @include flex-container();

      text-align: left;

      a,
      a:visited {
        @include flex-container();

        width: 100%;
        color: #444;

        // Border and padding operate together with the animation so that
        // the size of the contained element remains constant: as the border
        // grows on hover, the padding shrinks. This prevents reflow of the
        // contents of the link.
        border: {
          top: none;
          left: 1px solid transparent;
          bottom: none;
          right: 1px solid transparent;
        }

        padding: {
          left: 8px;
          right: 8px;
        }

        &:hover,
        &:active {
          color: $light_purple;
          border: {
            top: none;
            left: 8px solid $light_purple;
            bottom: none;
            right: 8px solid $light_purple;
          }

          padding: {
            left: 1px;
            right: 1px;
          }
        }
      }

      .title,
      .meta {
        margin: 0;
        padding-top: 0;
        color: $text;
        line-height: $lh_small;

        font: {
          size: $fsp;
          weight: 300;
        }

        span {
          display: block;
          margin: {
            top: 0;
            bottom: 0;
          }
          padding: {
            top: 0;
            bottom: 0;
          }
        }
      }

      .meta {
        @include flex(1 55%);
        font-family: $sans;
        padding: {
          top: 1px;
          left: 0.243rem;
        }

        #episode-date {
          font-style: italic;
        }
      }

      .title {
        @include flex(1 40%);
        font: {
          family: $slab;
          weight: 500;
        }
        text-align: right;
        padding-right: 0.243rem;

        #episode-title {
          text-transform: uppercase;
        }
      }
    }
  }
}

.category.nav {
  font-size: $fsh5;
  clear: both;
  background: $light_purple;

  margin-left: -0.4rem;
  margin-right: -0.4rem;
  border-left: 0.4rem solid $background;
  border-right: 0.4rem solid $background;

  @include flexbox();
  @include justify-content(space-between);
  @include align-content(flex-start);
  @include flex-flow(row wrap);

  .season-link {
    @include flex(1 0 auto);
    border: none;
    color: $background;
    text-align: center;
    padding: $fs_small / 2 $fs_small;

    &:hover,
    &:active {
      background: $pale_purple;
    }
  }

  .prev {
    text-align: left;
    padding-left: 12%;
  }

  .next {
    text-align: right;
    padding-right: 12%;
  }
}

footer {
  @include flexbox();
  @include justify-content(space-around);
  @include align-content(flex-start);
  @include flex-flow(row wrap);

  clear: both;

  margin-left: -0.4rem;
  margin-right: -0.4rem;
  border: 0.4rem solid $light_purple;
  border-top: none;

  background: $text;
  color: $background;
  text-align: center;
  padding-top: $fsh3;

  p,
  ul,
  ol,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-size: 90%;
  }

  p {
    text-align: left;
    font-weight: 300;
  }

  a,
  a:visited {
    color: $pale_purple;
  }

  .footer_item {
    width: 40%;
    max-width: 461px;
    padding-bottom: $fsh1;

    h1 {
      font-size: $fsh4;
      font-weight: 300;
      letter-spacing: 1px;
      color: $light_orange;
      text-shadow: 1px 1px 1px $background_darkened;
      border-bottom: 1px solid $background_darkened;
      margin-bottom: 1rem;
    }

    h2 {
      font-size: $fsh6;
      font-style: italic;
      font-weight: 400;
      padding-bottom: 0.444rem;
    }

    ul,
    ol {
      padding-bottom: $lh_em;
    }

    ul {
      list-style: none;
      padding-left: 0;
    }

    a i.fa {
      display: inline-block;
      padding-right: 5px;
      color: white;
    }
  }

  #social-wrapper {
    ul {
      text-align: left;
      padding-left: 8.889em;
      list-style: none;
    }
  }
}

/* Import media queries at the end to override the base */
@import 'media';
